import React from 'react';
import Header from '../header';
import Footer from '../footer';

export default function Stage37(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>37</strong> Sem que CHU perceba, voc chama seu chefe de lado e conta tudo a ele. O cientista vai at a sala dos robs e desliga todos. Ele examina os robs e ordena que vocs esperem na outra sala. Depois de algum tempo, ele volta e diz:</p>
			        <p>- Muito bem, cidado! Suas suspeitas estavam certas. Descobri que seu amigo sabotou um rob.</p>       
			        <p>CHU apenas tem tempo de olhar para voc surpreso, antes de ser pulverizado pela pistola laser do cientista. Voc recebe congratulaes do Computador e  promovido: agora pertence ao nvel de segurana vermelho. Voc torna-se um Agente Atirador. Sua misso  encontrar traidores e elimin-los. Infelizmente, pouca gente sobrevive por muito tempo nesse trabalho... mas no Complexo Alfa as coisas so assim mesmo.</p>
			        <p>Seja feliz, cidado.</p>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


