import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage13(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>13</strong> Voc olha para os lados e no v ningum. Procura cmeras e tambm no encontra nenhuma. , talvez no acontea nada. Com cuidado voc chega perto do rob, e ele parece no ligar para sua presena. Na cabea do rob voc pode ver um boto.</p>      
			        <Link className="btn-game" to="/game-paranoia/stages/09">Voc aperta o boto? V para 9.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/21">Enfia a mo no lixo e pega a chave? V para 21.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


