import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage17(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>17</strong>Voc e seu amigo so levados a uma sala gigantesca, dominada por um imenso monitor de TV e centenas de cmeras por toda parte. Um olho monstruoso surge na tela:  o Computador.</p>      
			        <p>Sem mais nem menos, um canho surge de uma abertura no teto e dispara em CHUA, transformando-o em uma poa de protoplasma fumegante.</p>
			        <p>- ELE ERA SUSPEITO POR ANDAR COM OS CADAROS DESAMARRADOS  diz o Computador.  ESTE EVENTO APENAS CONFIRMOU SUA CONDIO DE TRADOR. QUAL O SEU NVEL DE SEGURANA, CIDADO?</p>
			        <p>- Infravermelho, magnnimo Computador  voc responde suando aos litros.</p>
			        <p>- VOC  FELIZ, CIDADO?</p>     
			        <p>- Muito, generoso Computador.</p>
			        <p>- REVISTEM-NO.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/03">Voc tem consigo o papel que recebeu no banheiro AXK? V para 3.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/40">Se no tiver, v para 40.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


