import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage20(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>20</strong>  Que est fazendo, CHU?  voc pergunta.</p>     
			        <p> Nada  responde ele.  Bem, JOCA, voc  meu amigo e acho que posso lhe contar. Descobri que algum sabotou Um rob e estou tentando descobrir qual foi. Sabe como , se surgir algum problema, os culpados seremos ns. Mas vamos voltar, pois nosso amado chefe deve estar voltando.</p>     
			        <p>Vocs voltam para a outra sala.</p>      
			        <Link className="btn-game" to="/game-paranoia/stages/04">V para 4.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


