import React from 'react';
import Header from '../header';
import Footer from '../footer';
import {Link} from 'react-router-dom';

export default function Stage04(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
		        </div>
				<div className="game-stage">
			        <p><strong>4</strong> O cientista que comanda voc logo retorna. Voc pode contar a ele sobre a atitude suspeita de CHU, mas isto iria colocar seu amigo em grandes apuros. Por outro lado, se surgir algum problema, isso poderia livrar a sua cara.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/37">Se quiser contar, v para 37</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/11">Se achar melhor ficar quieto v para 11.</Link>
			    </div>
	    	</section>
	        <Footer/>
		</div>
	);
}


