import React from 'react';
import Header from '../header';
import Footer from '../footer';
import {Link} from 'react-router-dom';

export default function Stage07(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>7</strong> "Oh, no tem problema - diz o cidado vermelho - Voc acha que eu iria dedurar voc? Fique tranqilo, pode entrar no corredor vermelho. Ningum vai ficar sabendo".</p>     
			        <p>Voc desconfia, mas no pode desobedecer a um cidado de nvel superior. Quando entra no corredor vermelho e vai at o rob, ouve a gargalhada sdica do cidado vermelho enquanto ele aponta uma pistola laser:</p>
			        <p>- Traidor! Traidor! O Computador vai me recompensar! - e aperta o gatilho, reduzindo voc a pur de tomos.</p>      
			        <Link className="btn-game" to="/game-paranoia/stages/01">... v para 1.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


