import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage28(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>28</strong> Eu no vou meter a mo no lixo por voc, escria - diz ele.</p>
			        <p>- Ento - voc acusa - voc se recusa a colaborar com o Computador? Isso  traio!</p>
			        <p>- Ei, eu no disse isso! No pense que voc pode me enganar assim facilmente. Vou trazer o rob aqui para que voc mesmo verifique.</p>
			        <p>Ele vai at o rob, desliga e leva at voc. Voc pega a chave, agradece ao cidado vermelho e segue o seu caminho.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/14">V para 14.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


