import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage33(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>33</strong> Voc vai at o PLC, o servio de distribuio de equipamento. Bem antes de chegar l, pode ver duas filas. Na verdade, voc pode ver duas filas muito grandes. Ou melhor, voc pode ver duas filas gigantescas!</p>      
			        <p>Para melhorar sua situao, voc no tem idia de qual fila deve tomar. Felizmente h uma porta lateral no prdio onde diz INFORMAOES.</p>  
			        <Link className="btn-game" to="/game-paranoia/stages/29">Se quiser pegar a fila da esquerda, v para 29.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/32">Se quer a da direita, v para 32.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/19">Para pedir informaes, v para 19.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


