import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage35(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>35</strong> Voc anda calmamente pelo corredor quando v um rob de limpeza vindo em sua direo, em alta velocidade. O rob possui quatro braos, que carregam um esfrego, um balde, uma vassoura e uma p de lixo. Alm disso, h um cesto de lixo acoplado ao rob.</p>
			        <p>Conhecendo a pssima programao dos robs do Complexo Alfa, voc tenta sair do caminho  mas no consegue. O rob atropela voc, fazendo com que deixe cair a chave ALF. Ela vai parar na p de lixo do rob, que joga-a em sua lata de lixo e desaparece em uma esquina.</p>
			        <p>Voc persegue o manaco mecnico, mas perde-o de vista. Aqui existem apenas duas portas por onde ele possa ter entrado;</p>
			        <Link className="btn-game" to="/game-paranoia/stages/02">o banheiro AXK (v para 2)</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/16">ou o banheiro GHW (v para 16).</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


