import React from 'react';
import Header from '../header';
import Footer from '../footer';
import {Link} from 'react-router-dom';

export default function Stage08(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>8</strong>Voc pega a sua senha e espera calmamente a sua vez de ser atendido. Felizmente no leva muito tempo: em apenas uma hora chega a sua vez.</p>
			        <p>- Eu gostaria de saber qual fila devo tomar para pegar um chave ALF - voc pergunta ao balconista.</p>
			        <p>- Qual o seu Nvel de Segurana, cidado? - Infravermelho - voc responde.</p>
			        <p>- Desculpe, mas infelizmente esta informao no est disponvel para o seu Nvel de Segurana.</p>
			        <p>Feliz da vida, voc abandona a sala. </p>
			        <Link className="btn-game" to="/game-paranoia/stages/29">Se quiser pegar a fila da esquerda, v para 29. </Link>
			        <Link className="btn-game" to="/game-paranoia/stages/32">Se quer pegar a fila da direita, v para 32.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


