import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage11(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>11</strong> O cientista comea a prender voc com correias em um aparelho cheio de lminas, uma mquina de tortura que dever ser testada (e adivinhe quem ser a cobaia), quando um rob invade a sala e comea a quebrar tudo. Alguns guardas da Segurana Interna conseguem explodir o rob louco - e depois so eliminados, porque destruir propriedade do Computador  traio.</p>
			        <p>- Muito bem - rosna o cientista - Vocs estavam aqui sozinhos antes que eu sasse.  bem provvel que um de vocs tenha sabotado o rob. Terei que lev-los ao Computador para que isso seja resolvido.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/17">V para 17.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


