import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage21(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>21</strong>  melhor no se arriscar. Voc coloca a mo na lata de lixo  apenas para descobrir, da pior maneira, que a tampa se fecha assim que alguma coisa  colocada na lata. Sua mo fica presa e o rob recomea a andar rapidamente, arrastando voc pelos corredores. A boa notcia  que vocs saem do corredor vermelho; e a m notcia  que entram num corredor azul Calma, talvez nem tudo esteja perdido! Talvez o rob volte ao corredor vermelho antes que voc seja pulverizado.</p>
			        <p>De repente, o rob entra numa sala esmeralda onde esto umas trinta pessoas vestidas de azul. H uma faixa onde se l: "Reunio Anual dos Agentes Atiradores: Nvel de Segurana Azul".</p>
			        <p>Voc est com problemas. Mas, segundos depois, todos os seus problemas terminam, j que voc foi reduzido a uma pasta melequenta pelas armas dos Agentes Atiradores. Melhor recomear com outro clone.</p>     
			        <Link className="btn-game" to="/game-paranoia/stages/53">V para 53.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


