import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage31(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>31</strong> Voc espera um pouco e logo aparece um cidado de nvel de segurana vermelho. Ele se aproxima e ordena que voc beije seus ps. Depois de beijar suas botas fedorentas  porque deixar de obedecer a um superior  traio - o que voc ir dizer?</p>        
			        <Link className="btn-game" to="/game-paranoia/stages/07">"Senhor, preciso pegar um objeto que est no lixo daquele rob. Voc poderia trazer o rob at mim"? V para 7.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/28">"Cidado, o sbio computador me mandou verificar em todos os lixos se havia chaves ALF. Como aquele rob est em uma rea vermelha, onde no posso entrar, voc ficaria muito feliz em ajudar o Computador verificando por mim, no "? V para 28.</Link>     
				</div>
			</section>
			<Footer/>
		</div>
	);
}


