import React from 'react';
import Header from '../header';
import Footer from '../footer';
import {Link} from 'react-router-dom';

export default function Stage05(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>5</strong> O cientista ordena que voc espere aqui. Ele vai at a sala de testes buscar um novo equipamento, algo que no ir transform-lo em cinzas, voc espera. Felizmente voc no est sozinho. CHU TIB 1, seu melhor amigo, tambm trabalha aqui-e vocs podem ficar conversando enquanto ZECA no chega.</p>
			        <p>Durante a conversa, CHU pede licena para ir ao banheiro. Mas voc nota que ele entra em uma sala que no tem banheiro. Lembra-se tambm que, antes de sair, ele estava mexendo em uma estranha caixa preta sobre a mesa. Esquisito!</p>
			        <Link className="btn-game" to="/game-paranoia/stages/24">Se quiser seguir seu amigo, v para 24. </Link>
			        <Link className="btn-game" to="/game-paranoia/stages/39">Se fica esperando, v para 39. </Link>
			        <Link className="btn-game" to="/game-paranoia/stages/22">Se quer examinar a caixa, v para 22.</Link>
			    </div>
			</section>
			<Footer/>
		</div>
	);
}


