import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage30(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>30</strong> Voc consegue uma autorizao OGF43B-5, pega a chave ALF na outra fila e deixa o PLC, feliz porque cumpriu a funo designada por seu superior e, portanto no ser desintegrado por traio.</p>
			        <p>Voc escuta um "psiu" vindo de um corredor mal iluminado. Quando olha, algum que se esconde nas sombras e diz:</p>
			        <p>- Chuba-chuba prrrprrr hey!</p>
			        <p> a senha da sua sociedade secreta! Sim, voc pertence a um grupo que tenta derrubar a tirania do Computador. Claro que pertencer a uma sociedade secreta  traio. Traio  punida com a morte.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/12">Voc entra no corredor escuro para falar com ele? V para 12.</Link>       
			        <Link className="btn-game" to="/game-paranoia/stages/35">Voc continua seguindo seu caminho sem olhar para trs? V para 35.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


