import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage19(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>19</strong> Entrando na sala de informaes, voc descobre que ela est lotada de cidados infravermelhos. Voc recebe uma senha de nmero 345, e suspira aliviado: muito menos gente do que h nas filas l fora.</p>     
			        <Link className="btn-game" to="/game-paranoia/stages/08">Se quiser ficar com a senha e esperar sua vez, v para 8.</Link>      
			        <Link className="btn-game" to="/game-paranoia/stages/29">Se quer voltar e pegar a fila da esquerda, v para 29.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/32">Se quer a fila da direita, v para 32.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


