import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage29(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>29</strong> Voc pega a fila da esquerda e passa algumas horas prazerosas nela, contando seus prprios fios de cabelo para passar o tempo. Perdeu a conta algumas vezes, mas conseguiu contar todos os 68.457 antes que chegasse a sua vez.</p>        
			        <p>O balconista vestido de vermelho (um Nvel de Segurana superior ao seu, lembre-se) olha para voc e resmunga um sonolento "qui?". Voc, muito feliz, porque a felicidade  obrigatria, pede uma chave ALF para disjuntores potenciais.</p>
			        <p> Cad sua autorizao OGF43B-5  ele pergunta, quase dormindo.</p>
			        <p>Ah, claro. Aquele envelope preto tem uma autorizao, voc pensa. Abre o envelope e encontra um papel verde onde diz "Autorizao OGF43B-5". O que voc faz?</p>
			        <Link className="btn-game" to="/game-paranoia/stages/06">Diz "veia, eu tenho esta autorizao," e entrega o papel verde? V para 6.</Link>     
			        <Link className="btn-game" to="/game-paranoia/stages/32">Diz "eu no tenho, vou conseguir uma na fila da direita"? V para 32.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


