import React from 'react';
import Header from './header.js';
import Footer from './footer';
import {Link} from 'react-router-dom';

export default function Intro(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
		        <div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
		        </div>
				<div className="game-stage">
			        <p className="App-intro">Este  o Complexo Alfa. Uma metrpole futurista controlada por um computador louco, que se comporta como um tirano fascista. Totalmente paranico, o Computador v traidores em toda parte e zela pela felicidade dos cidados - nem que para isso precise matar todos! Voc  feliz cidado? No?! ZIOUF! Voc foi desintegrado, um clone est vindo substitu-lo...</p>
			        <p>O Complexo Alfa  completamente fechado, sem regies ao ar livre. Um vasto labirinto de corredores coloridos. As pessoas so classificadas por cores que determinam seus nveis de segurana, de acordo com os critrios do Computador. Desobedecer a um cidado de Nvel de Segurana superior ao seu  traio. Ser Comunista  traio (embora voc nem faa idia do que seja um Comunista). Pertencer a uma sociedade secreta  traio. Traio  punida com a morte.</p>
			        <p>O Computador preparou esta aventura-solo para divertir voc, cidado. Tome suas decises e siga as instrues. Se as instrues enviarem voc para parte alguma, ter sido devido a uma falha do Computador  mas, como o Computador nunca falha, encontrar falhas  traio. Deixar de seguir as instrues  traio. Traio  punida com a morte.</p>
			        <p>Tenha uma boa aventura, cidado.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/01">Comece em 1.</Link>
			    </div>
	        </section>
	        <Footer/>
		</div>
	);
}



