import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage32(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>32</strong> Voc pega a fila da direita, e passa trs horas divertidas nela. O nico incmodo  que o cidado na sua frente deve estar sofrendo de alguma disfuno intestinal, tornando o ar puro pouco agradvel ao olfato.</p>
			        <p>Quando chega a sua vez de ser atendido, o balconista pede a voc que espere um minutinho e abandona a sala. Ele retorna uma hora e quarenta minutos mais tarde:</p>
			        <p>- Ah, nada como um bom almoo! Muito bem, o que voc quer?</p>
			        <Link className="btn-game" to="/game-paranoia/stages/29">Se voc quer uma chave ALF no vai consegui-la aqui e ter que pegar a outra fila; v para 29.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/18">Se quer uma autorizao OGF43B-5, v para 18.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}




