import React from 'react';
import Header from '../header';
import Footer from '../footer';
import {Link} from 'react-router-dom';

export default function Stage03(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
		        <div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
		        </div>
				<div className="game-stage">
				    <p><strong>3</strong> Um soldado remexe em seus bolsos e acha um bilhete.</p>     
			        <p>Ele l em voz alta:</p>
			        <p>- "Todo o poder para os cidados infravermelhos."</p>
			        <p>Um texto de Karl Marx. Quem  esse cara? Voc pensa.</p>     
			        <p>- APENAS UM COMUNISTA TEM EM SUA POSSE MATERIAL COMUNISTA - diz o Computador. KARL MARX  UM COMUNISTA E VOC TEM UM BILHETE ESCRITO POR ELE.</p>
			        <p>O enorme e reluzente canho aponta em sua dire.  uma pena. Voc chegou to longe e morreu aqui, seu Comuna! Mas, bem... vamos facilitar um pouco, pois sabemos que voc vai roubar mesmo e recomear a aventura pela metade - afinal, s mesmo trapaceando se sobrevive no Complexo Alfa. </p>
			        <Link className="btn-game" to="/game-paranoia/stages/17">Volte para 17 e finja que voc no pegou o papel.</Link>
		    	</div>
	    	</section>
	        <Footer/>
		</div>
	);
}


