import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage23(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>23</strong> Assim que voc termina de falar, o balconista levanta a mo que segura o papel esmeralda. Esmeralda! O papel tambm no  disponvel para o nvel de segurana dele, que  vermelho.</p>
			        <p>- No  iss... - ele grita, mas no consegue terminar a frase antes de ser transformado em fumaa pelos rifles laser dos guardas.</p>
			        <p>- Bom trabalho, cidado - o comandante da Segni cumprimenta voc.  Por denunciar o traidor, sua lealdade ser comunicada ao Computador. Continue assim, e receber um nvel de segurana mais elevado. Seja feliz.</p>
			        <p>Manobra esperta. Por pouco voc no vira fumaa em vez do balconista.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/27">V para 27.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


