import React from 'react';
import {Link} from 'react-router-dom';
import Header from '../header';
import Footer from '../footer';

export default function Stage26(){
	return(
		<div className="App-game">
			<Header/>
			<section className="game">
				<div className="header-game">
		            <p>Esta  uma aventura-solo, um tipo de histria muito popular quando o RPG estava em seu incio no Brasil.  como uma histria comum, mas dividida em captulos numerados. Os captulos no fazem sentido se lidos em ordem numrica: ao final de cada trecho, voc deve seguir as instrues e fazer escolhas. As escolhas corretas levam voc ao melhor final, enquanto escolhas erradas levam a finais... desagradveis!</p>      
				</div>
				<div className="game-stage">
			        <p><strong>26</strong> Voc persegue o rob at que ele entra em um corredor vermelho, onde sua presena no  permitida. O safado pra a poucos metros de voc e comea solenemente a lavar o cho.</p>        
			        <p>Parece que voc est mesmo sem sorte. No fosse o corredor vermelho, seria s pegar a chave na lata de lixo e ir embora. Bem, voc pode arriscar  pode ser que ningum veja, e que nenhuma das cmeras de vdeo do computador esteja por a. Por outro lado, no vai ser possvel conseguir autorizao para pegar outra chave ALF, pois voc j recebeu uma. Voltar sem a chave significa desobedecer  ordem de um cidado de Nvel de Segurana superior, o que  sinal de traio. Traio  punida com a morte.</p>
			        <Link className="btn-game" to="/game-paranoia/stages/13">Quer entrar e pegar a chave? V para 13.</Link>
			        <Link className="btn-game" to="/game-paranoia/stages/31">Quer ficar esperando um milagre? V para 31.</Link>
				</div>
			</section>
			<Footer/>
		</div>
	);
}


